<template>
  <div class="seller" ref="seller">
    <div class="seller-content">
      <div class="overview">
        <div class="title">
          {{ seller.name }}
        </div>
        <div class="desc border-1px">
          <Star :size="36" :score="seller.score"></Star>
          <span class="text">({{ seller.ratingCount }})</span>
          <span class="text">{{ seller.sellCount }}</span>
        </div>
        <ul class="remark">
          <li class="block">
            <h2></h2>
            <div class="content">
              <span class="stress">{{ seller.minPrice }}</span>
            </div>
          </li>
          <li class="block">
            <h2></h2>
            <div class="content">
              <span class="stress">{{ seller.deliveryPrice }}</span>
            </div>
          </li>
          <li class="block">
            <h2></h2>
            <div class="content">
              <span class="stress">{{ seller.deliveryTime }}</span>
            </div>
          </li>
        </ul>
        <div class="favorite" @click="toggleFavorite">
          <i class="icon-favorite" :class="{'active': favorite}"></i>
          <span class="text">{{ favoriteText }}</span>
        </div>
      </div>
      <Split></Split>
      <div class="bulletin">
        <h1 class="title"></h1>
        <div class="content-wrapper border-1px">
          <p class="content">{{ seller.bulletin }}</p>
        </div>
        <ul class="supports" v-if="seller.supports">
          <li v-for="(support,index) in seller.supports" :key="index" class="supports-item border-1px">
            <span class="icon" :class="classmap[support.type]"></span>
            <span class="desc">{{ support.description }}</span>
          </li>
        </ul>
      </div>
      <Split></Split>
      <div class="pics">
        <h1 class="title"></h1>
        <div class="pic-wrapper" ref="picWrapper">
          <ul class="pic-list" ref="picList">
            <li class="pic-item" v-for="(pic, index) in seller.pics" :key="index">
              <img :src="pic" width="120" height="90">
            </li>
          </ul>
        </div>
      </div>
      <Split></Split>
      <div class="info">
        <h1 class="title border-1px"></h1>
        <ul>
          <li class="info-item border-1px" v-for="(info,index) in seller.infos" :key="index">{{ info }}</li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
import Star from '@/components/star/Star'
import Split from '@/components/split/Split'
import BScroll from 'better-scroll'
import { saveToLocal, loadFromLocal } from '@/common/js/store.js'

export default {
  props: {
    seller: {
      type: Object
    }
  },
  components: {
    Star,
    Split
  },
  data () {
    return {
      classmap: ['decrease', 'discount', 'special', 'invoice', 'guarantee'],
      favorite: ''
    }
  },
  computed: {
    favoriteText () {
      return this.favorite ? '' : ''
    }
  },
  watch: {
    seller () {
      // vuesellersellerDOMBscrollsellersellersellerseller
      if (this.seller.pics.length) {
        let picWidth = 120
        let margin = 6
        let width = (picWidth + margin) * this.seller.pics.length - margin
        this.$refs.picList.style.width = width + 'px'
      }
      // sellersellersellerfavoritecreatedfavoritecreatedsellerundefined
      this.favorite = loadFromLocal(this.seller.id, 'favorite', false)
    }
  },
  created () {
    // watch sellerloadFromLocalsellersellerloadFromLocalcreatedloadFromLocalfavorite
    this.favorite = loadFromLocal(this.seller.id, 'favorite', false)
  },
  mounted () {
    // mountedsellersellerwatch sellercreatedcreatedDOMthis.$refs.picListDOMmountedDOMDOM
    if (this.seller.pics) {
      let picWidth = 120
      let margin = 6
      let width = (picWidth + margin) * this.seller.pics.length - margin
      this.$refs.picList.style.width = width + 'px'
    }
    this.$nextTick(() => {
      // BScroll$nextTick()DOM
      this.initPageScroll()
      this.initPicListScroll()
    })
  },
  methods: {
    initPageScroll () { // 
      if (!this.scroll) {
        this.scroll = new BScroll(this.$refs.seller, {
          click: true
        })
      } else {
        this.scroll.refresh()
      }
    },
    initPicListScroll () {
      if (!this.picScroll) { // 
        this.picScroll = new BScroll(this.$refs.picWrapper, {
          click: true,
          scrollX: true, // 
          eventPassThrough: 'vertical' // 
        })
      } else {
        this.picScroll.refresh()
      }
    },
    toggleFavorite () { // 
      this.favorite = !this.favorite
      saveToLocal(this.seller.id, 'favorite', this.favorite)
    }
  }
}
</script>

<style lang="less" scoped>
@import '../../common/less/mixin.less';
.seller {
  position: fixed;
  top: 174px;
  left: 0;
  bottom: 0;
  width: 100%;
  overflow: hidden;
  .seller-content {
    .overview {
      position: relative;
      padding: 18px;
      .title {
        margin-bottom: 8px;
        line-height: 14px;
        color: rgb(7,17,27);
        font-size: 14px;
      }
      .desc {
        padding-bottom: 18px;
        font-size: 0;
        .border-1px(rgba(7,17,27,0.1));
        .star {
          display: inline-block;
          margin-right: 8px;
          vertical-align: top;
        }
        .text {
          display: inline-block;
          margin-right: 12px;
          line-height: 18px;
          vertical-align: top;
          font-size: 10px;
          color: rgb(77,85,93);
        }
      }
      .remark {
        display: flex;
        padding-top: 18px;
        .block {
          flex: 1;
          text-align: center;
          border-right: 1px solid rgba(7,17,27,0.1);
          &:last-child {
            border: none;
          }
          h2 {
            margin-bottom: 4px;
            line-height: 10px;
            font-size: 10px;
            color: rgb(147,153,159);
          }
          .content {
            line-height: 24px;
            font-size: 10px;
            color: rgb(7,17,27);
            .stress {
              font-size: 24px;
            }
          }
        }
      }
      .favorite {
        position: absolute;
        top: 18px;
        right: 11px;
        width: 50px;
        text-align: center;
        .icon-favorite {
          display: block;
          margin-bottom: 4px;
          line-height: 24px;
          font-size: 24px;
          color: #d4d6d9;
          &.active {
            color: rgb(240,20,20);
          }
        }
        .text {
          line-height: 10px;
          font-size: 10px;
          color: rgb(77,85,93);
        }
      }
    }
    .bulletin {
      padding: 18px 18px 0 18px;
      .title {
        margin-bottom: 8px;
        line-height: 14px;
        color: rgb(7,17,27);
        font-size: 14px;
      }
      .content-wrapper {
        padding: 0 12px 18px 12px;
        .border-1px(rgba(7,17,27,0.1));
        .content {
          line-height: 24px;
          font-size: 12px;
          color: rgb(240,20,20);
        }
      }
      .supports {
        .supports-item {
          padding: 16px 12px;
          font-size: 0;
          .border-1px(rgba(7,17,27,0.1));
          &:last-child {
            .border-none();
          }
          .icon {
            display: inline-block;
            vertical-align: top;
            width: 16px;
            height: 16px;
            margin-right: 6px;
            &.decrease {
              background: url("./decrease_4@2x.png");
              background-size: 16px 16px;
              background-repeat: no-repeat;
            }
            &.discount {
              background: url("./discount_4@2x.png");
              background-size: 16px 16px;
              background-repeat: no-repeat;
            }
            &.guarantee {
              background: url("./guarantee_4@2x.png");
              background-size: 16px 16px;
              background-repeat: no-repeat;
            }
            &.invoice {
              background: url("./invoice_4@2x.png");
              background-size: 16px 16px;
              background-repeat: no-repeat;
            }
            &.special {
              background: url("./special_4@2x.png");
              background-size: 16px 16px;
              background-repeat: no-repeat;
            }
          }
          .desc {
            line-height: 16px;
            font-size: 12px;
            color: rgb(7,17,27);
          }
        }
      }
    }
    .pics {
      padding: 18px;
      .title {
        margin-bottom: 12px;
        line-height: 14px;
        color: rgb(7,17,27);
        font-size: 14px;
      }
      .pic-wrapper {
        width: 100%;
        overflow: hidden;
        white-space: nowrap;
        .pic-list {
          font-size: 0;
          .pic-item {
            display: inline-block;
            margin-right: 6px;
            width: 120px;
            height: 90px;
            &:last-child {
              margin-right: 0;
            }
          }
        }
      }
    }
    .info {
      padding: 18px 18px 0 18px;
      .title {
        padding-bottom: 12px;
        line-height: 14px;
        color: rgb(7,17,27);
        font-size: 14px;
        .border-1px(rgba(7,17,27,0.1));
      }
      .info-item {
        padding: 16px 12px;
        line-height: 16px;
        .border-1px(rgba(7,17,27,0.1));
        font-size: 12px;
        color: rgb(7,17,27);
        &:last-child {
          .border-none();
        }
      }
    }
  }
}
</style>
