<template>
  <div class="goods">
    <div class="menu-wrapper" ref="menu">
      <ul>
        <li
        v-for="(item,index) in goods"
        :key="index"
        class="menu-item"
        :class="{'current': currentIndex === index}"
        @click="selectMenu(index)">
          <span class="text border-1px">
            <span v-if="item.type > 0" class="icon" :class="classmap[item.type]"></span>
            {{ item.name }}
          </span>
        </li>
      </ul>
    </div>
    <div class="foods-wrapper" ref="foods">
      <ul>
        <li v-for="(item,index) in goods" :key="index" class="food-list food-list-hook">
          <h1 class="title">{{ item.name }}</h1>
          <ul>
            <li v-for="food in item.foods" :key="food.name" @click="selectFood(food)" class="food-item border-1px">
              <div class="icon">
                <img width="57" height="57" :src="food.icon">
              </div>
              <div class="content">
                <h2 class="name">{{ food.name }}</h2>
                <p class="desc">{{ food.description }}</p>
                <div class="extra">
                  <span class="count">{{ food.sellCount }}</span>
                  <span>{{ food.rating }}%</span>
                </div>
                <div class="price">
                  <span class="now">{{ food.price }}</span><span class="old" v-if="food.oldPrice">{{ food.oldPrice }}</span>
                </div>
                <div class="cartcontrol-wrapper">
                  <!-- CartcontroladdonAddshopCartdrop -->
                  <Cartcontrol :food="food" @add="onAdd"></Cartcontrol>
                </div>
              </div>
            </li>
          </ul>
        </li>
      </ul>
    </div>
    <!--  -->
    <Shopcart :deliveryPrice="seller.deliveryPrice" :minPrice="seller.minPrice" :selectFoods="selectFoods" ref="shopCart"></Shopcart>
    <!-- foodFoodFoodaddonAddshopCartdrop -->
    <Food :food="selectedFood" ref="food" @add="onAdd"></Food>
  </div>
</template>

<script>
import BScroll from 'better-scroll'
import Shopcart from '@/components/shopcart/Shopcart'
import Cartcontrol from '@/components/cartcontrol/Cartcontrol'
import Food from '@/components/food/Food_anotherWay'

const ERR_OK = '200'
export default {
  props: ['seller'],
  data () {
    return {
      classmap: ['decrease', 'discount', 'special', 'invoice', 'guarantee'],
      goods: [],
      listHeight: [],
      scrollY: 0,
      selectedFood: {}
    }
  },
  components: {
    Shopcart,
    Cartcontrol,
    Food
  },
  computed: {
    currentIndex () { // food
      for (let i = 0; i < this.listHeight.length; i++) {
        let height1 = this.listHeight[i]
        let height2 = this.listHeight[i + 1]
        if (!height2 || (this.scrollY >= height1 && this.scrollY < height2)) {
          return i
        }
      }
      return 0
    },
    selectFoods () { // food.countselectFoods
      let foods = []
      this.goods.forEach((good) => {
        good.foods.forEach((food) => {
          if (food.count) {
            foods.push(food)
          }
        })
      })
      return foods
    }
  },
  created () {
    this.axios.get('/goods').then(res => {
      if (res.data.errno === ERR_OK) {
        this.goods = res.data.data.goods
        this.$nextTick(() => {
          // mountedDOMv-forv-ifv-showmountedDOMnextTick()DOM
          // $nextTick()  DOM  DOM
          this.initScroll() // BSscroll
          this.caculateHeight() // DOM
        })
      }
    })
  },
  methods: {
    initScroll () { // BSscroll
      this.menuScroll = new BScroll(this.$refs.menu, {
        click: true // better-scrolltouch
      })
      this.foodsScroll = new BScroll(this.$refs.foods, {
        click: true,
        probeType: 3 // 
      })
      this.foodsScroll.on('scroll', (position) => {
        // BScroll scroll 
        this.scrollY = Math.abs(Math.round(position.y))
      })
    },
    caculateHeight () { // 
      let foodList = this.$refs.foods.getElementsByClassName('food-list-hook')
      let height = 0
      this.listHeight.push(height)
      for (let i = 0; i < foodList.length; i++) {
        let item = foodList[i]
        height += item.clientHeight
        this.listHeight.push(height)
      }
    },
    selectMenu (index) { // 
      let foodList = this.$refs.foods.getElementsByClassName('food-list-hook')
      let el = foodList[index]
      this.foodsScroll.scrollToElement(el, 300) // better-scrollAPIscrollToElement(, )
    },
    onAdd (el) { // addshopCartdrop
      this.$refs.shopCart.drop(el)
    },
    selectFood (food) { // 
      this.selectedFood = food
      this.$refs.food.show() // 
    }
  }
}
</script>

<style lang="less" scoped>
@import "../../common/less/mixin.less";
.goods {
  display: flex;
  position: absolute;
  top: 174px;
  bottom: 46px;
  width: 100%;
  overflow: hidden;
  .menu-wrapper {
    flex: 0 0 80px;
    width: 80px;
    background-color: #f3f5f7;
    .menu-item {
      display: table; // 
      height: 53px;
      width: 56px;
      line-height: 14px;
      padding: 0 12px;
      &.current {
        position: relative;
        z-index: 10;
        margin-top: -1px;
        background-color: #fff;
        .text {
          .border-none();
          font-weight: 700;
        }
      }
      .icon {
        display: inline-block;
        vertical-align: top;
        width: 12px;
        height: 12px;
        margin-right: 2px;
        &.decrease {
          background: url("./decrease_3@2x.png");
          background-size: 12px 12px;
          background-repeat: no-repeat;
        }
        &.discount {
          background: url("./discount_3@2x.png");
          background-size: 12px 12px;
          background-repeat: no-repeat;
        }
        &.guarantee {
          background: url("./guarantee_3@2x.png");
          background-size: 12px 12px;
          background-repeat: no-repeat;
        }
        &.invoice {
          background: url("./invoice_3@2x.png");
          background-size: 12px 12px;
          background-repeat: no-repeat;
        }
        &.special {
          background: url("./special_3@2x.png");
          background-size: 12px 12px;
          background-repeat: no-repeat;
        }
      }
      .text {
        display: table-cell; // table
        width: 56px;
        vertical-align: middle;
        .border-1px(rgba(7,17,27,0.1));
        font-size: 12px;
      }
      &:last-child > .text {
        .border-none();
      }
    }
  }
  .foods-wrapper {
    flex: 1;
    background-color: #fff;
    .title {
      padding-left: 14px;
      height: 26px;
      line-height: 26px;
      border-left: 2px solid #d9dde1;
      font-size: 12px;
      color: rgb(147, 153, 159);
      background-color: #f3f5f7;
    }
    .food-item {
      display: flex;
      margin: 18px;
      padding-bottom: 18px;
      .border-1px(rgba(7,17,27,0.1));
      &:last-child {
        .border-none();
        margin-bottom: 0;
      }
      .icon {
        flex: 0 0 57px;
        margin-right: 10px;
      }
      .content {
        flex: 1;
        .name {
          margin: 2px 0 8px 0;
          height: 14px;
          line-height: 14px;
          font-size: 14px;
          color: rgb(7, 17, 27);
        }
        .desc, .extra {
          line-height: 10px;
          font-size: 10px;
          color: rgb(147, 153, 159);
        }
        .desc {
          margin-bottom: 8px;
          line-height: 12px;
        }
        .extra {
          .count {
            margin-right: 12px;
          }
        }
        .price {
          font-weight: 700;
          line-height: 24px;
          .now {
            margin-right: 8px;
            font-size: 14px;
            color: rgb(240, 20, 20);
          }
          .old {
            text-decoration: line-through;
            font-size: 10px;
            color: rgb(147, 153, 159);
          }
        }
        .cartcontrol-wrapper {
          position: absolute;
          right: 0;
          bottom: 12px;
        }
      }
    }
  }
}
</style>
