// localstorage
export function saveToLocal (id, key, value) {
  let seller = window.localStorage.__seller__
  if (!seller) {
    seller = {}
    seller[id] = {}
  } else {
    seller = JSON.parse(seller) // localStorageJSON
    if (!seller[id]) {
      seller[id] = {}
    }
  }
  seller[id][key] = value
  // sellerlocalStorage
  window.localStorage.__seller__ = JSON.stringify(seller)
}
// localstorage
export function loadFromLocal (id, key, def) {
  let seller = window.localStorage.__seller__
  if (!seller) {
    return def
  }
  seller = JSON.parse(seller)[id]
  if (!seller) {
    return def
  }
  let ret = seller[key]
  return ret || def
}
